    let Caçar;
    let Cronometro;
    let HoraDaCaçada = 60;
    function Cronometrar(){
        Cronometro = setInterval(() => {
            HoraDaCaçada--
        },1000)
    }
    function IniciarACaçada(){
        Caçar = setInterval( () => {
            Ritmo = 3000;
            Perseguição()
        },60000)
    }
    function acelerar(teste){
        if(teste){
            clearInterval(Movimentar)
            if(Ritmo >= 1000){
                Ritmo -= 1000
            }else if(Ritmo < 1000){
                Ritmo -= 100;
                if(Ritmo < 100){
                    Ritmo = 90;
                }
            }
            SaveIndice += 60;
            Perseguição(MudandoDeFase);
        }
    }
    function Tudodnv(){
