            {MovimentaçãoPeloMapa("CIMA")} <!--para cima-->
        
        {:else if (code == "ArrowDown") || (code == "KeyS")}

            {MovimentaçãoPeloMapa("BAIXO")} <!--para baixo-->

        {:else if (code == "ArrowLeft") || (code == "KeyA")}

            {MovimentaçãoPeloMapa("ESQUERDA")} <!--para esquerda-->

        {:else if (code == "ArrowRight") || (code == "KeyD")}

            {MovimentaçãoPeloMapa("DIREITA")} <!--para direita-->

        {/if}
{/if}

{#if MudandoDeFase !== "vitoria"}

<button class="ButtonVoltar" on:click={() => trocarEstadoDoJogo('menu')}><p class="Voltar">Voltar</p></button>

{/if}

<div>
	<audio id="audio" autoplay loop>
		<source src="/css/sons/PlayMusicGame.wav" type="audio/wav">
		<source src="/css/sons/PlayMusicGame.ogg" type="audio/ogg">
	</audio>
</div>

{#if MudandoDeFase == "tutorial"}
    
    <p class='FasesDoJogo'>Tutorial</p>
    <p class="Enigma">Para uma melhor experiência recomendamos utilizar fones de ouvidos</p>
    {#if !enigma}
        
    <p class="textofutil">
    {RenderizandoMapa()}
    {DeterminandoEixos(MudandoDeFase)}
    </p>
    <div id="DicaTutorial" class="aimds">
        <ul><h2 class="h2">Como jogar?</h2></ul>
        <ul class="info">↟ ou W para Cima</ul>
        <ul class="info">↡ ou S para Baixo</ul>
        <ul class="info">↠ ou D para a Direita</ul>
        <ul class="info">↞ ou A para a Esquerda</ul>
    </div>
    <table class="mapa" align="center" id="mapatutorial">
        {#each mapa0 as linhas,i}
        {#if LimiteY <= i && LimiteY + (Dimensionamento * 2) >= i}
            <tr>
                {#each linhas as elementos,j}
                {#if LimiteX <= j && LimiteX + (Dimensionamento * 2) >= j}
                    {#if elementos == 0}
                        <th class="chaoturorial"><img src="/css/imagens/chaotutorial.png" alt="chao"></th>
                    {:else if elementos == 1}
                        <th><img src="/css/imagens/paredetutorial.png" alt="parede"></th>
                    {:else if elementos == "X"}
                        <th><img src="/css/imagens/saida.png" alt="saida"></th>
                    {:else if elementos == "DANTE"}
                        <th class="Dante0"><img src="/css/imagens/Dante.png" alt="personagem"></th>
                    {/if}
                {/if}
                {/each}
            </tr>
        {/if}
        {/each}
    </table>
        {:else}

        <p class='Enigma'>Sempre que passar de fase, haverá um enigma a ser solucionado. </p>
        <p class='Enigma'>Lembre-se: Existe um limite de tempo para resolver os enigmas. </p>
        <p class='Enigma'>Ao atingir o limite de tempo para resolver o enigma em qualquer nível você voltará para o primeiro.</p>
        <p class="Enigma">OBS: Só serão aceitas letras maiúsculas nas respostas de todos os enigmas.</p>
        <p class='Enigma'>Nenhuma das palavras-chave contém qualquer acento.</p>
        <p class="Enigma">Após compreender o funcionamento do Minos Labyrinth, digite: "OK" e poderá prosseguir para a primeira fase.</p>
        <input bind:value={PalavraChave} on:keydown={Alterando(PalavraChave == "OK",MudandoDeFase)} placeholder="APENAS LETRAS MAIUSCULAS" class='RespostaEnigma'>
        {#each mapa0 as linhas}
            <tr class="minimapa">
            {#each linhas as elementos}
            {#if elementos == 0}
            <th><img class="imgmini" id="chao0" src="/css/imagens/chaotutorial.png" alt="chao"></th>
        {:else if elementos == 1}
            <th><img class="imgmini" src="/css/imagens/paredetutorial.png" alt="parede"></th>
        {:else if elementos == "X"}
            <th><img class="imgmini" src="/css/imagens/saida.png" alt="saida"></th>
        {:else if elementos == "DANTE"}
            <th><img class="imgmini" id="chao0" src="/css/imagens/chaotutorial.png" alt="personagem"></th>
        {/if}
            {/each}
        </tr>
        {/each}

    {/if}
    {:else if MudandoDeFase == "nivel1"}

    <p class='FasesDoJogo'>Nivel 1</p>

    {#if !enigma}
        
    <p class="textofutil">
        {IniciarACaçada()}
        {Cronometrar()}
        {acelerar(Indice == SaveIndice)}
        {clearInterval(Tempo)}
        {RenderizandoMapa()}
        {DeterminandoEixos(MudandoDeFase)}
    </p>
    {#if HoraDaCaçada > 0}
        <p class="Enigma">O Minotauro ira surgir em: {HoraDaCaçada}</p>
    {:else if Ritmo == 3000}
        <p class="Enigma">O Minotauro começou a perseguição</p>
    {:else if Ritmo == 2000}
        <p class="Enigma">O Minotauro acelerou o passo</p>
    {:else if Ritmo == 1000}
        <p class="Enigma">O Minotauro começou a correr</p>
    {:else if Ritmo < 1000}
        <p class="Enigma">O Minotauro está enfurecido</p>
    {/if}
    <table class="mapa" align="center" id="mapanivel1">
        {#each mapa1 as linhas,i}
        {#if LimiteY <= i && LimiteY + (Dimensionamento * 2) >= i}
            <tr>
                {#each linhas as elementos,j}
                {#if LimiteX <= j && LimiteX + (Dimensionamento * 2) >= j}
                    {#if elementos == 0}
                        <th ><img src="/css/imagens/chaonivel1.png" alt="chao"></th>
                    {:else if elementos == 1}
                        <th><img src="/css/imagens/paredenivel1.png" alt="parede"></th>
                    {:else if elementos == "Y"}
                        <th><img src="/css/imagens/saida.png" alt="saida"></th>
                    {:else if elementos == "DANTE"}
                        <th class="Dante1"><img src="/css/imagens/Dante.png" alt="personagem"></th>
                    {:else if elementos == 8}
                        <th><img src="/css/imagens/saida.png" alt=8></th>
                    {:else if elementos == "MINOS"}
                        <th><img src="/css/imagens/minotauro.png" alt="monstro"></th>
                    {/if}
                    {/if}
                {/each}
            </tr>
            {/if}
        {/each}
    </table>
    <div id="ContoDoMinotauro" class="aimds">
        <ul class="info">Há muito tempo atrás, mais tempo do que todas as suas gerações podem ter passado na terra,
        Minotauro nasceu da relação da rainha de Creta com um touro dado por Poseidon.
        O rei tomado pela raiva tentou matar o animal,
        mas Poseidon interviu todas as vezes pela vida de sua criação.</ul>
    </div>
        {:else}
        <p class="textofutil">
            {TempoEnigma()}
            {ResertarContador()}
            {ProximoEnigma()}
        </p>
        <p class="Enigma">O que achou das provações resultantes de sua ações precipitadas?</p>
        <p class="Enigma">Deveria tomar cuidado, este não é um labirinto comum e os guardiões deste lugar não gostam de visitantes inesperados.</p>
        <p class="Enigma">Responda-me cautelosamente, deuses não costumam ser piedosos como tanto propagam.</p>
        <p class="EnigmaPergunta">{Perguntas[NumeroEscolhido] }</p>
        <p class="Contador">{contador}s</p>
    <input bind:value={PalavraChave} on:keydown={Alterando(PalavraChave == respostas[NumeroEscolhido] ,MudandoDeFase)} placeholder="APENAS LETRAS MAIUSCULAS" class='RespostaEnigma'>
    {#each mapa1 as linhas}
    <tr class="minimapa">
        {#each linhas as elementos}
        {#if elementos == 0}
            <th><img class="imgmini" id="chao1" src="/css/imagens/chaonivel1.png" alt="chao"></th>
        {:else if elementos == 1}
            <th><img class="imgmini" src="/css/imagens/paredenivel1.png" alt="parede"></th>
        {:else if elementos == "Y"}
            <th><img class="imgmini" src="/css/imagens/saida.png" alt="saida"></th>
        {:else if elementos == "DANTE"}
            <th><img class="imgmini"id="chao1" src="/css/imagens/chaonivel1.png" alt="personagem"></th>
        {:else if elementos == 8}
            <th><img class="imgmini" src="/css/imagens/saida.png" alt=8></th>
        {/if}
        {/each}
    </tr>
    {/each}

        {/if}
    {:else if MudandoDeFase == "nivel2"}

    <p class='FasesDoJogo'>Nivel 2</p>

    {#if !enigma}
     
    <p class="textofutil">
        {IniciarACaçada()}
        {Cronometrar()}
        {acelerar(Indice == SaveIndice)}
        {clearInterval(Tempo)}
        {RenderizandoMapa()}
        {DeterminandoEixos(MudandoDeFase)}
    </p>
    {#if HoraDaCaçada > 0}
        <p class="Enigma">O Minotauro ira surgir em: {HoraDaCaçada}</p>
    {:else if Ritmo == 3000}
        <p class="Enigma">O Minotauro começou a perseguição</p>
    {:else if Ritmo == 2000}
        <p class="Enigma">O Minotauro acelerou o passo</p>
