        <p class="Enigma">O Minotauro começou a correr</p>
    {:else if Ritmo < 1000}
        <p class="Enigma">O Minotauro está enfurecido</p>
    {/if}
    <table class="mapa" align="center" id="mapanivel2">
        {#each mapa2 as linhas,i}
        {#if LimiteY <= i && LimiteY + (Dimensionamento * 2) >= i}
            <tr>
                {#each linhas as elementos,j}
                {#if LimiteX <= j && LimiteX + (Dimensionamento * 2) >= j}
                    {#if elementos == 0}
                        <th><img src="/css/imagens/chaonivel2.png" alt="chao"></th>
                    {:else if elementos == 1}
                        <th><img src="/css/imagens/paredenivel2.png" alt="parede"></th>
                    {:else if elementos == "Z"}
                        <th><img src="/css/imagens/saida.png" alt="saida"></th>
                    {:else if elementos == "DANTE"}
                        <th class="Dante2"><img src="/css/imagens/Dante.png" alt="personagem"></th>
                    {:else if elementos == 8}
                        <th><img src="/css/imagens/saida.png" alt=8></th>
                    {:else if elementos == 7}
                        <th><img src="/css/imagens/paredefalsanivel2.png" alt="paredefalsa"></th>
                    {:else if elementos == "MINOS"}
                        <th><img src="/css/imagens/minotauro.png" alt="monstro"></th>
                    {/if}
                    {/if}
                {/each}
            </tr>
        {/if}
        {/each}
    </table>
    <div id="ContoDoMinotauro" class="aimds">
        <ul class="info">Os deuses acataram a ira e súplicas do rei, mas não iriam contra Poseidon por um mero mortal. Então, com uma idéia de Atena, decidiram aprisionar o monstro em um labirinto. Desde então Minotauro vive de suas caçadas, e posso afirmar que ele sabe bem como tratar seus visitantes.</ul>
    </div>
        {:else}
        <p class="textofutil">

            {TempoEnigma()}
            {ResertarContador()}
            {ProximoEnigma()}
        </p>
        <p class="Enigma">Gostei de você, jovem.</p>
        <p class="Enigma">Como pôde perceber, nem tudo é o que parece.</p>
        <p class="Enigma">Espero não acostuma-lo mal, mas por enquanto vou aconselha-lo a não confiar tanto no que seus olhos vêem.</p>
        <p class='Enigma'>Paredes falsas podem parecer algo impensável para humanos, mas não me compare com seres como vocês.</p>
        <p class="EnigmaPergunta">{Perguntas[NumeroEscolhido] }</p>
        <p class="Contador">{contador}</p>
        <input bind:value={PalavraChave} on:keydown={Alterando(PalavraChave == respostas[NumeroEscolhido] ,MudandoDeFase)} placeholder="APENAS LETRAS MAIUSCULAS" class='RespostaEnigma'>
        {#each mapa2 as linhas}
        <tr class="minimapa">
        {#each linhas as elementos}
        {#if elementos == 0}
            <th><img class="imgmini" id="chao1" src="/css/imagens/chaonivel2.png" alt="chao"></th>
        {:else if elementos == 1}
            <th><img class="imgmini" src="/css/imagens/paredenivel2.png" alt="parede"></th>
        {:else if elementos == "Z"}
            <th><img class="imgmini" src="/css/imagens/saida.png" alt="saida"></th>
        {:else if elementos == "DANTE"}
            <th><img class="imgmini" id="chao1" src="/css/imagens/chaonivel2.png" alt="personagem"></th>
        {:else if elementos == 8}
            <th><img class="imgmini" src="/css/imagens/saida.png" alt=8></th>
        {:else if elementos == 7}
            <th><img class="imgmini" src="/css/imagens/paredefalsanivel2.png" alt="paredefalsa"></th>
        {/if}
        {/each}
    </tr>
    {/each}

        {/if}
    {:else if MudandoDeFase == "nivel3"}

    <p class='FasesDoJogo'>Nivel 3</p>

    {#if !enigma}
     
    <p class="textofutil">
        {IniciarACaçada()}
        {Cronometrar()}
        {acelerar(Indice == SaveIndice)}
        {clearInterval(Tempo)}
        {RenderizandoMapa()}
        {DeterminandoEixos(MudandoDeFase)}
    </p>
    {#if HoraDaCaçada > 0}
        <p class="Enigma">O Minotauro ira surgir em: {HoraDaCaçada}</p>
    {:else if Ritmo == 3000}
        <p class="Enigma">O Minotauro começou a perseguição</p>
    {:else if Ritmo == 2000}
        <p class="Enigma">O Minotauro acelerou o passo</p>
    {:else if Ritmo == 1000}
        <p class="Enigma">O Minotauro começou a correr</p>
    {:else if Ritmo < 1000}
        <p class="Enigma">O Minotauro está enfurecido</p>
    {/if}
    <table class="mapa" align="center" id="mapanivel3">
        {#each mapa3 as linhas,i}
        {#if LimiteY <= i && LimiteY + (Dimensionamento * 2) >= i}
            <tr>
                {#each linhas as elementos,j}
